.App {
  text-align: center;
  flex-direction: row;
  background-color: #f3f3f3;
  margin: auto;
  align-items: center;
  justify-content: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* * {
  border: solid 1px purple;
} */

img.cover {
  box-shadow: rgba(0, 0, 6, 0.9) 0px 7px 29px 0px;
  z-index: 10;
}

#playlist {
  display: flex;
  background-color: white;
  flex-direction: column;
  text-align: left;
  width: 700px;
  height: 500px;
  border-radius: 0 43px 43px 0;
  padding: 1rem 4rem 1rem 3rem;
  overflow: auto;
}

