.heading {
  color: #a7a7a7;
  font-size: 21px;
}

.track {
  font-size: 28px;
  color: #4e4e4e;
  margin: 12px;
  display: flex;
  align-items: center;
}

.strike {
  color: rgb(175, 175, 175);
  text-decoration-line: line-through;
}

button {
  background-color: transparent;
  border: none;
}

.track-name {
  display: inline-block;
  padding-left: 1rem;
}
